* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    overflow: hidden;
}

.App {
    /*background: green;*/
    background: #282c34;
    min-height: 100vh;
    display: flex;
    font-size: calc(10px + 2vmin);
    color: white;
}

.h1__end{
    font-size: 80px;
    width: 100vw;
    text-align: center;
}

.grid {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.x {
    position: absolute;
    top: 50%;
    width: 100vw;
    height: 2px;
    background: white;
}

.y {
    position: absolute;
    left: 50%;
    width: 2px;
    height: 100%;
    background: white;
}
.fabricBlock {
    /*margin: 5px;*/
    position: absolute;
    /*background-image: url("assets/img/jute.jpg");*/
    width: 100vw;
    height: 75%;
    /*top: 50%;*/
}

.jute{
    /*background-image: url("assets/img/jute.jpg");*/
    width: 100%;
    height: 100%;
}

.denim{
    /*background-image: url("assets/img/denim.webp");*/
    width: 100vw;
    height: 100%;
}

.denim > .row{
    height: 2px;

}

.cotton{
    /*background-image: url("assets/img/katoen.webp");*/
    width: 100vw;
    height: 100%;
}

.lines{
    /*margin: 5px;*/
    position: absolute;
    /*background-image: url("assets/img/jute.jpg");*/
    width: 100%;
    height: 75%;
}

.row{
    position: absolute;
    height: 5px;
    width: 100vw;
    background: red;
}
.column{
    position: absolute;
    height: 100%;
    width: 5px;
    background: blue;
}

.buttonBox{
    position: absolute;
    bottom: 0;
    height: 25%;
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.answer {
    width: 50%;
    height: 100%;
    background: #282c34;
    z-index: 1;
    font-size: 90px;
    color: white;
}
